@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');

body {
  background-color: #25282b;
  color: #FFF;
  font-family: 'Righteous', sans-serif;
  font-size: 20px;
}

#drum-machine {
  outline: 1px solid #FFF;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 2fr 1fr;
  justify-items: center;
  align-items: center;
  background-color: #43474a;
  padding: 20px;
  box-shadow: 10px 10px 10px 10px rgba(0,0,0,0.1);
}

div.drum-pad {
  width: 100px;
  height: 75px;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  background-color: #f04a24;
  border-radius: 6px;
  border: groove 1px #f04a24;
  box-shadow: 7px 7px 5px 0 rgba(0,0,0,0.2);
  margin: 10px;
  cursor: pointer;
}

div.drum-pad:active, div.drum-pad-active {
  border: ridge 1px #f04a24;
  top: 2px;
  box-shadow: 0 0 0 0;
}

#buttons-grid {
  display: grid;
  grid-template-columns: 120px 120px 120px;
}

#display {
  width: 200px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

#display-box {
  background-color: #f04a24;
  padding: 6px 0;
  height: 25px;
  margin: 0 0 10px 20px;
}

#toggler-box {
  background-color: #000;
  width: 45px;
  height: 20px;
  margin: auto;
  border: solid 2px #000;
  border-radius: 11px;
}

#toggler-inner {
  width: 20px;
  height: 20px;
  background-color: #f04a24;
  cursor: pointer;
  border-radius: 100%;
}

footer {
  position: absolute;
  top: 76%;
  left: 50%;
  transform: translate(-50%, -50%);
}

footer > a {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  letter-spacing: 1px;
}

@media (max-width: 621px) {
  #drum-machine {
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr;
  }

  footer {
    top: 86%;
  }
}